import React, { Component } from "react";
import Grid from "../../common/Grid";

class Books extends Component {
    
    model = TapList.getModel("Tap", [
        "geo_area_id",
        "li_event_dst_ip",
        "li_event_dst_port",
        "li_rtp_dst_ip",
        "li_rtp_base_port",
        "li_rtp_port_count",
        "actions",
      ]);
    
      static getModel = (msgPrefix, idList) => {
        return idList.map((id) => new GridProperty(id, `${msgPrefix}.${id}`));
      };

    render() {
        return (
            <div>
                <h1>Book: My React App!</h1>
            </div>
        );
    }
}

export default Books;